import React, { useContext, useEffect, useState } from 'react';
import { isMobile, isIOS } from 'react-device-detect';

import PaperText from '../components/paperText';
import {
    Avatar,
    Card,
    CardActionArea,
    CardMedia,
    Chip,
    Fade,
    Grid,
    makeStyles,
    Snackbar,
    Tooltip,
    Zoom
} from '@material-ui/core';
import '../pages/globals.css';
import NavigationBar from '../components/navigation';
import SocialButton from '../components/socialButton';
import buscanlogo from '../assets/buscanlogo.png';
import MobileMenu from '../components/mobileMenu';
import jorL from '../assets/jorLogo.png';
import hacemosL from '../assets/ejesProv/hsf.jpg';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import jorFondo from '../assets/fondo/jor1cel.jpg';
import jorFondoweb from '../assets/fondo/jor1.jpg';
import useProgressiveImage from '../helpers/progressiveImg';
import MessengerCustomerChat from 'react-messenger-customer-chat';
import NoticiasView from './noticiasView';
import Loader from '../components/loader';
import colores from '../styles/colors';
import { Link } from 'react-router-dom';

import { FormContext } from '../context/formContext';
import { Alert } from '@material-ui/lab';

export default function Home() {
    const [openSnack, setopenSnack] = useState(false);
    const [dblClick, setDblClick] = useState(false);
    const styles = useStyles();
    const im = isMobile
        ? jorFondo
        : 'https://jorweb.s3.sa-east-1.amazonaws.com/fijas/jor1.jpg';
    const imag = useProgressiveImage(im);

    const onBackButtonEvent = (e) => {
        e.preventDefault();
        if (dblClick) return window.history.back();
        setopenSnack(true);
        setDblClick(true);
        setTimeout(() => {
            setDblClick(false);
        }, 700);
    };

    useEffect(() => {
        window.history.pushState(null, null, window.location.pathname);
        window.addEventListener('popstate', onBackButtonEvent);
        return () => window.removeEventListener('popstate', onBackButtonEvent);
    }, []);

    if (!imag) {
        return <Loader />;
    }

    return (
        <>
            <MessengerCustomerChat
                language="es_AR"
                pageId={process.env.REACT_APP_PAGE_FB_ID}
                appId={process.env.REACT_APP_FB_ID}
                htmlRef="https://jorweb.herokuapp.com/"
            />

            <Snackbar
                anchorOrigin={{
                    vertical: isMobile ? 'bottom' : 'top',
                    horizontal: 'left'
                }}
                open={openSnack}
                autoHideDuration={3000}
                onClose={() => setopenSnack(false)}
            >
                <Alert onClose={() => setopenSnack(false)} severity="info">
                    clickea 2 veces para salir
                </Alert>
            </Snackbar>
            <Fade in={true} timeout={600}>
                <div
                    className={styles.fullscreen}
                    style={{ backgroundImage: `url(${imag})` }}
                >
                    <div className={styles.floatTop}>
                        {!isMobile ? <SocialButton /> : <MobileMenu />}
                    </div>

                    {isMobile && (
                        <div
                            style={{
                                position: 'absolute',
                                top: 28,
                                left: 95
                            }}
                        >
                            <img
                                alt="jorgelina mudallel logo"
                                src={jorL}
                                style={{ height: 85, marginBottom: 10 }}
                            />
                        </div>
                    )}

                    {!isMobile && <DirectAcc />}

                    <div className={styles.descJor}>
                        {!isMobile && (
                            <img
                                alt="jorgelina mudallel"
                                src={jorL}
                                style={{
                                    height: isMobile ? 85 : 150,
                                    marginBottom: 25
                                }}
                            />
                        )}

                        {isMobile && <ChipsDirect />}

                        <PaperText />
                    </div>

                    {!isMobile && <NoticiasView />}
                    {!isMobile && <NavigationBar />}
                </div>
            </Fade>
        </>
    );
}

const ChipsDirect = () => {
    return (
        <div
            style={{
                flexDirection: 'row-reverse',
                display: 'flex'
            }}
        >
            {directAcc.map((btn, i) => (
                <Zoom in={true} timeout={500}>
                    <Chip
                        key={i}
                        onClick={() => {
                            window.open(btn.url);
                        }}
                        avatar={<Avatar src={btn.img} />}
                        style={{ margin: 3 }}
                    />
                </Zoom>
            ))}
        </div>
    );
};

const DirectAcc = () => {
    const classes = useStyles();
    return (
        <div
            style={{
                position: 'absolute',
                right: 55,
                top: 55,
                flexDirection: 'column',
                display: 'flex'
            }}
        >
            <Tooltip key="hsf" title="Sumate a Hacemos Santa Fe">
                <Card className={classes.cardHSF}>
                    <CardActionArea>
                        <Link to={'/sumate'}>
                            <CardMedia
                                style={{
                                    height: 140,
                                    borderRadius: '25%'
                                }}
                                image={hacemosL}
                            />

                            <Chip
                                className={classes.chipHSF}
                                label="SUMATE"
                                variant="outlined"
                            />
                        </Link>
                    </CardActionArea>
                </Card>
            </Tooltip>
            <div
                style={{
                    width: 140
                }}
            >
                <Grid container spacing={1}>
                    {directAcc.map((btn, i) => (
                        <Zoom key={btn.title} in={true} timeout={300 * i + 500}>
                            <Grid item sm={4}>
                                <Tooltip key={btn.title} title={btn.title}>
                                    <Card
                                        key={i}
                                        className={classes.accesodirectoweb}
                                        onClick={() => {
                                            window.open(btn.url);
                                        }}
                                    >
                                        <CardActionArea>
                                            <CardMedia
                                                style={{
                                                    height: 40
                                                }}
                                                image={btn.img}
                                            />
                                        </CardActionArea>
                                    </Card>
                                </Tooltip>
                            </Grid>
                        </Zoom>
                    ))}
                </Grid>
            </div>
        </div>
    );
};

const directAcc = [
    {
        id: 'BEG',
        img: begIcon,
        url: 'https://sso.santafe.gov.ar/service-auth/login?service=https://www.santafe.gob.ar/boletoeducativo/login',
        title: 'inicia el registro'
    },
    {
        id: 'billetera',
        img: billetera,
        url: isIOS
            ? 'https://apps.apple.com/ar/app/billetera-santa-fe/id1558196556'
            : 'https://play.google.com/store/apps/details?id=com.pluspagos.mobile',
        title: 'descarga la app'
    },
    {
        id: 'vacunas',
        img: vacuna,
        url: 'https://www.santafe.gob.ar/santafevacunacovid/inicio',
        title: 'inscribite y recibi la vacuna'
    },
    {
        id: 'buscan',
        img: buscanlogo,
        url: 'https://play.google.com/store/apps/details?id=com.colodev.buscan',
        title: 'BusCan: descarga la app'
    }
];

const useStyles = makeStyles((theme) => ({
    fullscreen: {
        minHeight: '100vh',
        height: '100vh',
        padding: '0 1.5rem',
        display: 'flex',
        flexDirection: 'column-reverse',
        backgroundPosition: '75% center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    },
    typography: {
        padding: theme.spacing(2)
    },
    floatTop: { position: 'absolute', top: isMobile ? 50 : 0 },
    descJor: {
        position: 'absolute',
        bottom: isMobile ? '8%' : '30%',
        width: isMobile ? '88%' : '36%',
        maxWidth: 480,
        marginLeft: isMobile ? 'auto' : '10%',
        marginRight: isMobile ? 'auto' : null
    },
    cardHSF: {
        height: 140,
        width: 140,
        marginBottom: 15,
        borderRadius: '25%',
        overflow: 'hidden'
    },
    chipHSF: {
        position: 'absolute',
        bottom: -1.5,
        left: '11%',
        letterSpacing: 2,
        fontSize: 10,
        height: 25,
        width: '80%',
        borderWidth: 2,
        backgroundColor: '#ededed',
        color: colores.gamBdark
    },
    accesodirectoweb: {
        height: 40,
        width: 40,
        borderRadius: '25%',
        margin: 'auto',
        transition: `${theme.transitions.create(['transform'])}`,
        '&:hover': {
            transform: 'scale(1.1)'
        }
    }
}));
